import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DesignComponent } from './design.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component:  DesignComponent
  },
  {
    path: 'area',
    loadChildren: () => import('./area/area.module').then(m => m.AreaModule)
  },
  {
    path: 'design-menu',
    loadChildren: () => import('./design-menu/design-menu.module').then(m => m.DesignMenuModule)
  },
  {
    path: 'design-label',
    loadChildren: () => import('./design-barcode/design-barcode.module').then(m => m.DesignBarcodeModule)
  },
  {
    path: 'kitchen',
    loadChildren: () => import('./kitchen/kitchen.module').then(m => m.KitchenModule)
  },
]

@NgModule({
  declarations: [DesignComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class DesignModule { }
