import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-design',
  templateUrl: './design.component.html',
  styleUrls: ['./design.component.scss']
})
export class DesignComponent implements OnInit {

  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    private languageService: LanguageService
  ) { }

  ngOnInit(): void {
  }
  gotoArea() {
    if (this.vhAuth.checkMyPermission('area_enable_manage_area')) {
      this.router.navigate(['/cafe/dashboard/management/design/area']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoDesignMenu() {
    this.router.navigate(['/cafe/dashboard/management/design/design-menu']);
  }

  gotoDesignLabel(){
    this.router.navigate(['/cafe/dashboard/management/design/design-label']);
  }

  gotoKitchen() {
    if (this.vhAuth.checkMyPermission('kitchen_enable_manage_kitchen')) {
      this.router.navigate(['/cafe/dashboard/management/design/kitchen']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
